export default {
	data() {
		return {
			cuIconList: null
		}
	},
	onShow: function() {
		uni.onSocketMessage((res) => {
			this.getMessage(JSON.parse(res.data))
		})
		getApp().sendSocket("getFuncList")
	},
	methods: {
		getMessage: function(data) {
			uni.hideLoading()
			let msg = data.msg
			let obj = data.obj
			if (msg == "loginError") {
				getApp().delCode()
				uni.redirectTo({
					url: "/pages/mine/mine"
				})
			} else if (msg == "funcList") {
				this.cuIconList = obj
			}
		},
		barTo: function(e) {
			let name = e.currentTarget.dataset.name
			if (name == "scan") {
				getApp().scan()
			} else {
				getApp().barTo(name)
			}
		},
		action: function (e) {
			let name = e.currentTarget.dataset.act
			if (name == "assetImport") {
				uni.scanCode({
					onlyFromCamera: true,
					success: (res) => {
						uni.navigateTo({
							url: "/pages/func/assetimport?no=" + res.result
						})
					}
				})
				// uni.navigateTo({
					// url: "/pages/func/assetimport?no=" + res.result,
					// url: "/pages/func/assetimport?"
				// })
			} else if (name == "borrowlend") {
				uni.scanCode({
					onlyFromCamera: true,
					success: (res) => {
						uni.navigateTo({
							url: "/pages/scan/scan?no=" + res.result + "&action=true"
						})
					}
				})
			} else {
				uni.navigateTo({
					url: '/pages/func/' + name
				})
			}
		}
	}
}
